.myButton{
    padding: 10px;
    /* height: 30px; */
    margin: 0 auto;
    width: 100%;
    /* width: auto; */
    background: rgb(255, 255, 255);
    border: none;
    border-radius:5px;
    cursor: pointer;
    color: rgb(12, 12, 13);
}
.myButton:hover{
    opacity: 0.9;
}
.myButton a{
    text-decoration: none;
    color: rgb(12, 12, 13);
}