.task {
  /* padding: 7.5px 0; */
  /* border: 1px solid #000; */
  /* border: 1px solid black; */
  position: relative;
}

.taskDiv {
  display: grid;
  grid-template-columns: 1fr 50px;
  /* border-bottom: 1px solid rgb(97, 218, 251); */
  /* padding-bottom: 10px; */
  padding: 7.5px 0;
}
.taskDiv p {
  align-self: center;
  text-align: left;
  word-break: break-all;
  font-size: 16px;
  /* padding-right: 20px; */
  cursor: pointer;
  user-select: none;
  color: rgb(97, 218, 251);
}

.taskHeader {
  /* position: relative; */
  padding-left: 40px;
}
.LineunderTask{
  width: 100%;
  border-bottom: 1px solid rgba(97, 218, 251,0.1);
  margin: 0 auto;
}

.emojiTicker {
  font-size: 21px;
  width: 40px;
  text-align: center;
  align-self: flex-start;
  position: absolute;
  left: 0;
  right: 0;
  /* top: -6px; */
  /* top: 5px; */
  /* top: 2px; */
  top: 7px;
  bottom: 0;
}
.taskHeaderOver h5 {
}
span{
  pointer-events: none; 
  /* АХУЕТЬ! */
}
.taskDiv p[data-complete="true"] {
  opacity: 0.2;
  text-decoration: line-through;
}
