@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  /* color: rgb(12, 12, 13); */
}
body{
  /* background-color: #270092; */
  background-color: #282c34;
  height: 100%;
  height: 100vh;
  /* overflow-y: hidden; */
}
.App {
  max-width: 600px;
  height: 100%;
  height: 100vh;
  text-align: center;
  display: grid;
  grid-template-columns: 83px 1fr;
  font-size: 18px;
  color: white;
  margin: 0 auto;
  border-left: 1px solid rgba(97, 218, 251, 0.1);
  border-right: 1px solid rgba(97, 218, 251, 0.1);
}
.taskBlock{
  margin-top: 20px;
}
.taskBlockWrap::-webkit-scrollbar {
  display: none;
}
.taskBlockInputAndButtons{
  top: 0;
  width: calc(100% - 83px);
  max-width: 516px;
  position: fixed;
  z-index: 999;
  background:rgb(40, 44, 52);
  padding-bottom: 5px;
}



.buttonTask{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}
.CategorySmile{
  width: 100%;
  height: 50px;
  display: grid;
  grid-template-columns: 20px 1fr;
  padding: 0 5px;
  
}
.CategorySmile p{
  font-size: 25px;
  user-select: none;
  cursor: pointer;
}
.CategorySmile p:hover{
  opacity: 0.5;
}
.taskBlockWrap{
  overflow-y: scroll;
  scrollbar-width: none;
  position: relative;
  /* margin-top: 57px; */
  /* padding: 57px 0 50px; */
}
.classesBlock{
  /* padding: 0 0 50px; */
  /* border-right: 5px solid rgba(97, 218, 251, 0); */
  border-right: 1px solid rgb(97, 218, 251, 0.1);
  overflow-x: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
  position: relative;
}
.taskBlockEmpty{
  height: 57px;
}
.classesBlockEmpty{
  height: 50px;
}

.classesBlockNavbar{
  position: fixed;
  top: 0;
  background-color: #282c34;
}
.classesBlock::-webkit-scrollbar {
  display: none;
}

.CategorySmile a{
  text-decoration: none;
}
.CategorySmile a:active{
  text-decoration: none;
}
.CategorySmile a:hover{
  text-decoration: none;
}
.CategorySmile a:link{
  text-decoration: none;
}
.CategorySmile a:visited{
  text-decoration: none;
}
.buttonDelete{
  padding: 5px;
  background: none;
  border: none;
  cursor: pointer;
  color: #ff5252;
  align-self: center;
  text-align: center;
  margin: 0 auto;
  width: 20px;
  margin: 0 auto;
  font-size: 15px;
}
.buttonDelete img{
  width: 15px;
  

}
.CategorySmileLink{
  align-self: center;
  text-align: center;
  margin: 0 auto;

}
.smileCategoryWrap{
  display: grid;
  /* grid-template-columns: 1fr 40px; */
  grid-template-columns: 40px 1fr;
}
.smileCategory{
  padding-top: 5px;
  margin: 0 auto;
}